import {
	mapActions
} from 'vuex'
import uniPopup from '../uni-ui/uni-popup/uni-popup.vue'
import uniNumberBox from '../uni-ui/uni-number-box/uni-number-box.vue'
export default {
	name: "",
	components: {
		uniPopup,
		uniNumberBox
	},
	props: {
		collectFlag: {
			type: Boolean,
			default: false
		},
		cartsNum: {
			type: Number,
			default: 0
		},
		// 商品信息
		goodsinfo: {
			type: Object,
			default: () => {}
		},
		// 登录状态
		loginStatus: {
			type: Boolean,
			default: false
		},
		// openId
		openId: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			// 用来判断是点击的加入购物车还是立即购物按钮
			confirmNum: 0,
			// 步进器的值
			goodsNum: 1
		}
	},
	methods: {
		...mapActions(['addToCarts', 'getCartsNum']),
		// 打开购买弹窗
		openPop(nums) {
			this.confirmNum = nums
			this.$refs.popup.open()
		},
		// 关闭按钮
		closePop() {
			this.$refs.popup.close()
		},
		// 步进器方法
		bindChange(e) {
			this.goodsNum = e
		},
		// 子组件分发回父组件的收藏方法
		collectBtn(e) {
			this.$emit('collect', e)
		},
		// 跳转购物车
		goToCarts() {
			uni.switchTab({
				url: '../../pages/carts/carts'
			})
		},
		// 确定按钮
		confirmBtn() {
			if (this.confirmNum === 0) {
				this.addToCarts({
					goodsId: this.goodsinfo.id,
					number: this.goodsNum,
					openId: this.openId
				})
				this.goodsNum = 1
				this.$refs.popup.close()

				this.getCartsNum('liuhong')

			} else {
				let orderArr = []
				orderArr.push(this.goodsinfo)
				orderArr = JSON.stringify(orderArr)
				uni.navigateTo({
					url: `../../pages/settlement/settlement?orderArr=${orderArr}&allPrice=${this.checkedPrices}&payNum=${this.goodsNum}`,
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	},
	mounted() {

	},
	onLoad() {

	},
	filters: {

	},
	computed: {
		checkedPrices() {
			if (this.confirmNum > 0) {
				return this.goodsNum * this.goodsinfo.retail_price
			}
		}
	},
	watch: {

	},
	directives: {

	}
}
